import PropTypes from 'prop-types';
// Design Resources
import { Spin } from 'antd';
// Hooks
import { useLoading } from '../../hooks';
// Components
import { Instruction, Title, Translate } from '../../components/shared';
import Card from './Card';

function StepCategorySelection({ currentCategories, onSendChosenSide }) {
  const [isLoading] = useLoading();

  return (
    <div className="o-card-selection">
      <Title>
        <Translate
          pt="Medium, selecione uma das duas categorias"
          en="Psychic, select one of the two categories"
        />
      </Title>
      <Instruction contained>
        <Translate
          pt="Como medium, seu objetivo  ajudar as pessoas escolherem a posio correta do medidor de ondas telepticas. Primeiro, escolha uma das duas cartas que voc acredita ser capaz de criar uma boa dica."
          en="As the Psychic, your goal is to help the other players to find the correct position of the needle in the wavelength measuring device. But first, choose one of these cards that you believe you will be able to write a good clue."
        />
        {isLoading && (
          <div>
            <Spin />
          </div>
        )}
      </Instruction>

      <div className="o-card-selection__container">
        {currentCategories.map((card) => (
          <button
            key={`card-button-${card.id}`}
            className="o-card-selection__button"
            onClick={() => onSendChosenSide({ categoryId: card.id })}
            disabled={isLoading}
          >
            <Card left={card.left} right={card.right} />
          </button>
        ))}
      </div>
    </div>
  );
}

StepCategorySelection.propTypes = {
  currentCategories: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.string,
      left: PropTypes.string,
      right: PropTypes.string,
    })
  ),
  onSendChosenSide: PropTypes.func,
};

export default StepCategorySelection;
